.hero {
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: url('/img/s_hero/s_hero.jpg');
  background-position: top;
  -webkit-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-repeat: no-repeat;

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 20vh;
    background-color: $cb_d;
    z-index: 1; }

  &__mark-up {
    bottom: 1em;
    margin-left: 10%;
    font-size: 1em;
    width: 50%;
    background-color: $ct_a;
    z-index: 2;
    @include r(1024) {
      width: 80%;
      bottom: 2em;
      margin-left: 5%;
      padding-bottom: 1em; } }

  &__psevdo-logo {
    font-size: .75em;
    margin-top: 1em;
    margin-left: 1em; }

  &__section-title {
    font-size: 1em;
    margin-top: 0.5em;
    margin-left: 1.75em;
    padding-bottom: 1em; } }

