.navbar {
  display: block;
  font-size: 1.5rem;
  // background-color: $cb_d
  max-width: 1680px;
  margin: 0 auto;
  padding: 0;
  &__list {
    display: none;
    float: right;
    width: 100%;
    text-align: center;
    margin-bottom: 70px;
    @include rmin(1025) {
      display: inline-block;
      width: 50%;
      margin: 0; } }
  &__item {
    display: none;
    @include rmin(1025) {
      display: block;
      float: right;
      margin-left: 2.4em; } }
  &__link {
    display: inline-block;
    text-decoration: none;
    line-height: 5rem;
    font-weight: 300;
    color: $ct_b;
    text-shadow: 0 0 5px rgba(0,0,0,0.5);
    // +rmin(1025)
    //   line-height: 12rem
    &:hover {
      color: $ct_a; } }
  &.menu_state_open {
    background-color: $cb_d; }
  &.menu_state_open .menu__icon span:nth-child(1) {
    top: 11px;
    width: 0%;
    left: 50%; }
  &.menu_state_open .menu__icon span:nth-child(2) {
    transform: rotate(45deg); }
  &.menu_state_open  .menu__icon span:nth-child(3) {
    transform: rotate(-45deg); }
  &.menu_state_open  .menu__icon span:nth-child(4) {
    bottom: 11px;
    width: 0%;
    left: 50%; }
  &.menu_state_open &__item {
    display: block; }
  &.menu_state_open &__list {
    display: inline-block; }
  &.menu_state_open .logo__link {
    display: block;
    text-align: center;
    padding-top: 30px; } }

.menu {
  &__icon {
    display: block;
    float: right;
    margin: 1.5rem 0;
    margin-right: 2.4rem;
    width: 30px;
    height: 24px;
    position: relative;
    cursor: pointer;
    & span {
      display: block;
      position: absolute;
      width: 100%;
      height: 2px;
      left: 0;
      background-color: $ct_b;
      border-radius: 2px;
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
      -webkit-transition: 0.25s ease-in-out;
      -moz-transition: 0.25s ease-in-out;
      -ms-transition: 0.25s ease-in-out;
      -o-transition: 0.25s ease-in-out;
      transition: 0.25s ease-in-out;
      &:nth-child(1) {
        top: 0; }
      &:nth-child(2), &:nth-child(3) {
        top: 11px; }
      &:nth-child(4) {
        bottom: 0; } }
    @include rmin(1025) {
      display: none; } } }
.line {
  max-width: 1680px;
  margin: 0 auto;
  border-bottom: 1px solid $cb_d;
  border-top: 1px solid $ct_b; }
