  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 360px) / (1200 - 360)));
  line-height: 1.4;
}

@media (max-width: 360px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1200px) {
  body {
    font-size: 50px;
  }
}

/* глобальные параметры */
* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: 'Rubik', sans-serif;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.clearfix:after {
  display: block;
  content: "";
  clear: both;
}

.wrap {
  max-width: 1680px;
  margin: 0 auto;
}

.wrap__video iframe {
  overflow: hidden;
  height: 100%;
  width: 100%;
  max-width: 720px;
  min-height: 576px;
  margin: 0 auto;
}

.wrap__content {
  max-width: 1200px;
  margin: 0 auto;
}

/* конец глобальных параметров */
