.navbar {
  display: block;
  font-size: 1.5rem;
  max-width: 1680px;
  margin: 0 auto;
  padding: 0;
}

.navbar__list {
  display: none;
  float: right;
  width: 100%;
  text-align: center;
  margin-bottom: 70px;
}

@media only screen and (min-width: 1025px) {
  .navbar__list {
    display: inline-block;
    width: 50%;
    margin: 0;
  }
}

.navbar__item {
  display: none;
}

@media only screen and (min-width: 1025px) {
  .navbar__item {
    display: block;
    float: right;
    margin-left: 2.4em;
  }
}

.navbar__link {
  display: inline-block;
  text-decoration: none;
  line-height: 5rem;
  font-weight: 300;
  color: #ffffff;
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.navbar__link:hover {
  color: #d8de3e;
}

.navbar.menu_state_open {
  background-color: #636159;
}

.navbar.menu_state_open .menu__icon span:nth-child(1) {
  top: 11px;
  width: 0%;
  left: 50%;
}

.navbar.menu_state_open .menu__icon span:nth-child(2) {
  transform: rotate(45deg);
}

.navbar.menu_state_open .menu__icon span:nth-child(3) {
  transform: rotate(-45deg);
}

.navbar.menu_state_open .menu__icon span:nth-child(4) {
  bottom: 11px;
  width: 0%;
  left: 50%;
}

.navbar.menu_state_open .navbar__item {
  display: block;
}

.navbar.menu_state_open .navbar__list {
  display: inline-block;
}

.navbar.menu_state_open .logo__link {
  display: block;
  text-align: center;
  padding-top: 30px;
}

.menu__icon {
  display: block;
  float: right;
  margin: 1.5rem 0;
  margin-right: 2.4rem;
  width: 30px;
  height: 24px;
  position: relative;
  cursor: pointer;
}

.menu__icon span {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  background-color: #ffffff;
  border-radius: 2px;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -ms-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

.menu__icon span:nth-child(1) {
  top: 0;
}

.menu__icon span:nth-child(2), .menu__icon span:nth-child(3) {
  top: 11px;
}

.menu__icon span:nth-child(4) {
  bottom: 0;
}

@media only screen and (min-width: 1025px) {
  .menu__icon {
    display: none;
  }
}

