/* глобальные параметры */
* {
  box-sizing: border-box; }

html {
  font-size: 62.5%;
  font-family: 'Rubik', sans-serif; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.clearfix:after {
  display: block;
  content: "";
  clear: both; }

.wrap {
  max-width: 1680px;
  margin: 0 auto;
  &__video iframe {
    overflow: hidden;
    height: 100%;
    width: 100%;
    max-width: 720px;
    min-height: 576px;
    margin: 0 auto; } }

.wrap__content {
  max-width: 1200px;
  margin: 0 auto; }
/* конец глобальных параметров */




.section {
  /* delete */
  height: 100vh;
  width: 100%;
  background-color: $cb_d;
  /* end delete */
  &-title {
    font-weight: 500;
 } }    // font-size: 1.25em

.title {
  &_dark {
    color: $ct_i; }
  &_light {
    color: $ct_b; }

  &_middle {
    margin-top: 1em;
    font-size: 1em;
    @include r(540) {
      font-size: 1.5em; } }
  &_small {
    margin-top: 1em;
    font-size: 0.3em;
    @include r(540) {
      font-size: 0.6em; } } }

.item {
  &-title {
    font-size: 1.5em;
    font-weight: 500;
    color: $ct_b;
    margin-top: 1em;
    margin-bottom: 1em; }

  &__descriptions {
    font-size: 1.1em;
    line-height: 2em;
    font-weight: 300;
    list-style: square;
    color: $ct_b;
    margin-left: 1.1em; } }



.centered {
  width: 100%;
  text-align: center; }
.down {
  margin-bottom: 3em; }

.mark-title {
  &:before {
    display: inline-block;
    width: .25em;
    height: 1em;
    margin-right: 0.5em;
    content: "";
    background-color: $ct_a; } }

/*кнопка в начало - перенести */
.totop {
  position: fixed;
  bottom: .5em;
  right: .25em;
  height: 2em;
  width: 2em;
  line-height: 2em;
  text-align: center;
  font-size: 1em;
  -webkit-border-radius: 2em;
  border-radius: 2em;
  background-color: $ct_a;
  color: $ct_i;
  text-decoration: none;
  &:hover {
    color: #FFF; } }
